/*FONT-SHADOWS-FONT-SHADOWS-FONT-SHADOWS-FONT-SHADOWS-FONT-SHADOWS-FONT-SHADOWS-FONT-SHADOWS-FONT-SHADOWS-FONT-SHADOWS-*/

.font-black-outline-light{
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000, -1px -1px 0 #000, rgb(0, 0, 0) 1px 0 4px,
  rgb(0, 0, 0) 1px 0 2px ; 
}

.font-red-outline-light{
  text-shadow: -1px 1px 0 #6a0000, 1px 1px 0 #6a0000, 1px -1px 0 #6a0000, -1px -1px 0 #6a0000, rgb(114, 0, 0) 1px 0 4px,
  rgb(113, 0, 0) 1px 0 2px ; 
}



.font-black-outline-strong{
  text-shadow: -1px 4px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000, -1px -1px 0 #000, rgb(0, 0, 0) 4px 0 10px,
  rgb(62, 62, 62) 4px 0 10px ; 
}

.font-shadow-white{
  text-shadow:rgba(255, 255, 255, 0.87) 0px 0 4px,
  rgb(255, 255, 255) 0px 0 50px ;
}

.font-shadow-white-light{
  text-shadow:
  rgba(255, 255, 255, 0.87) 0px 0 4px,
  rgb(255, 255, 255) 0px 0 10px ;
}

.font-black-outline-white-shadow{
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000, -1px -1px 0 #000, rgba(255, 255, 255, 0.87) 0px 0 4px,
  rgb(255, 255, 255) 0px 0 50px ; 
}

.font-bg-gradient {
  background:linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8), rgba(0, 0, 0, 0.6)), url(./background-images/stripes.webp);
  background-position: cover;
  background-size: 125px;
  background-clip: text;
  
  -webkit-background-clip: text; /* For Safari */
  color: transparent;
}

.menu:hover{
  
  text-shadow:rgba(255, 255, 255, 0.87) 0px 0 4px,
  rgb(255, 255, 255) 0px 0 50px ;
  
}

