.bg-dna::before {
  content: "";
  background-image: url("./images/dna.png");
  background-position: center;
  background-size: 26px;
  background-repeat: round;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.4;
  z-index: 0;
}





.bg-striped-content{
  
  background:linear-gradient(rgba(40, 40, 40, 0.78), rgba(0, 0, 0, 0.85)), url(./background-images/striped-mesh.jpg);
  background-position: cover;
  background-size: 500px;
  
}

.bg-striped-content-red{
  
  background:linear-gradient(rgba(83, 0, 0, 0.85), rgba(105, 0, 0, 0.85)), url(./background-images/striped-mesh.jpg);
  background-position: cover;
