.corners:before, 
.corners:after, 
.corners>:last-child:before, 
.corners>:last-child:after {
    position: absolute;
    width: 10px; 
    height: 10px;
    border-color: rgb(0, 0, 0); 
    border-style: solid;
    content: ' ';
    opacity: 0.4;
    
}

.corners:before {
    top: 20px;
    left: 20px;
    border-width: 2px 0 0 2px
}

.corners:after {
    top: 20px;
    right: 20px;
    border-width: 2px 2px 0 0
}

.corners>:last-child:before {
    bottom: 20px;
    right: 20px;     
    border-width: 0 2px 2px 0
}

.corners>:last-child:after {
    bottom: 20px;
    left: 20px;
    border-width: 0 0 2px 2px
}

.top-neg-5{
  top: -1.25rem
}
.top-neg-20{
  top: -5rem;
}

.max-height-125{
  max-height: 125%;
}

.w-initial{
  width: initial;
}

.PRO-ONE-DIV{
  
}

.BULB{
  
  transition: top ease-in 800ms;
}

.TOP-BULB{
  top: -20%;
}

.WINDOW{
  height: 50%;
}



.WARMLIGHT-PROJECTS{
  top: 20%;
  opacity: 1;
  width: initial;
  height: initial;
}

.WARMLIGHT-ABOUT{
  top: 69%;
  opacity: 0.5;
  filter: grayscale(80%);
}
.SILHOUTTE{
  height: 70vh;
  min-height: 500px;
}

.HOME-SCREEN{
  min-height: -webkit-fill-available;
}

.PROJECT-SECTION, .ABOUT-SECTION{
  width: 50%;
}



.CONTACT-SECTION {
  width: 400px;
}

.CONTACT-FORM{
  width: 100%;
}

.CONTACT-FORM > input, .CONTACT-FORM > textarea, .CONTACT-FORM > button {
  width: 90%;
}

.PROJECT-CONTAINER{
  grid-template-columns: repeat(2, 1fr);
  
}




.TITLE{
  grid-column:1/2;
  grid-row: 1/2;
}

.DEMO{
  grid-column: 1/4;
  grid-row: 1/2;
}

.INSIDE-INFO{
  display: none;
  height: 25%;
  width: 15%;
}

.DETAIL{
  
  grid-column: 1/2;
  grid-row: 2/3;
  
}

.FEATURE{
  
  grid-column: 2/3;
  grid-row: 2/3;
  
}

.TOOLS{
  grid-column: 2/3;
  grid-template-columns: repeat(6, 1fr);
}

.PROJECT-ONE-IMAGE {
  background:linear-gradient(rgba(40, 40, 40, 0.78), rgba(0, 0, 0, 0.85)), url(./images/dummyimage.png);
  background-position: cover;
  background-size:auto;
}

.INTRO-DEV{
  font-size: 0.9rem;
}



@media (max-width: 1440px) {
  .PROJECT-SECTION, .ABOUT-SECTION{
    width: 60%;
  }
  .TOP-BULB{
    top: 0%;
  }
  .TOOLS{
    grid-template-columns: repeat(3, 1fr);
  }
  .ABOUT-SKILLS-TEXT{
    display: none;
  }
}
@media (max-width: 1200px) {
  
  .INSIDE-INFO{
    display:inherit;
  }
  .SIDE-INFO{
    display: none;
  }
  .PROJECT-SECTION, .ABOUT-SECTION {
    width: 70%;
  }

  .WINDOW{
    height: 25%;
  }
  
}

@media (max-width: 992px) {
  .PROJECT-SECTION{
    width: 80%;
  }
}

@media (max-width: 768px) {
  .PROJECT-SECTION, .ABOUT-SECTION, .CONTACT-SECTION {
    width: 90%;
  }
  .PROJECT-CONTAINER  {
    grid-template-columns: 1fr;
  }
  .DEMO{
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .DETAIL{
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .FEATURE{
    grid-column: 1/2;
    grid-row: 3/4;
    
  }
}

@media (max-width: 480px) {
  .PROJECT-CONTENT-RIGHT {
    grid-template-columns: 1fr;
  }
  .DETAIL{
    height: 24rem;
  }
  .INTRO-DEV{
    font-size: 0.5rem;
  }
  
  .INTRO-NAME{
    font-size: 1.5rem;
    line-height: 1.5rem;
  }

  

  .SILHOUTTE{
    object-fit: cover;
  }
  
}

@media (max-width: 300px) {
  .APP{
    width: 300px;
  }
  
  
  
  
  .PROJECT-CONTENT-RIGHT {
    grid-template-columns: 1fr;
  }
  .DETAIL{
    height: 24rem;
  }
  .TOOLS{
    grid-template-columns: repeat(3, 1fr);
  }
}


.rotate {
  animation: rotate 2s linear infinite;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

/*UTILITY-UTILITY-UTILITY-UTILITY-UTILITY-UTILITY-UTILITY-UTILITY-UTILITY-UTILITY-UTILITY-UTILITY-*/

.flip{
  transform: scaleX(-1);
}



